import React from 'react';
import { Route, Switch } from 'react-router-dom';
import NetworkServicesSLAProduction from './NetworkServicesSLAProduction/NetworkServicesSLAProduction';
import NetworkServicesSLAPreproduction from './NetworkServicesSLAPreproduction/NetworkServicesSLAPreproduction';
import NetworkSupport from './NetworkSupport/NetworkSupport';

export default class ServiceLevels extends React.Component {
    render() {
        const { match } = this.props;

        return (
            <Switch>
                <Route
                    path={`${match.url}/network-services-sla-production`}
                    component={NetworkServicesSLAProduction}
                />
                <Route
                    path={`${match.url}/network-services-sla-preproduction`}
                    component={NetworkServicesSLAPreproduction}
                />
                <Route
                    path={`${match.url}/network-support-sla`}
                    component={NetworkSupport}
                />
            </Switch>
        );
    }
}
