import React from 'react';
import { Route, Switch } from 'react-router-dom';
import Index from './Index/Index';
import I20191206 from './20191206/20191206';
import I20200213 from './20200213/20200213';
import Event1 from './event-0001/event-0001';
import Event3 from './event-0003/event-0003';
import Event4 from './event-0004/event-0004';
import Event5 from './event-0005/event-0005';
import Event7 from './event-0007/event-0007';
import Event8 from './event-0008/event-0008';
import Event9 from './event-0009/event-0009';

export default class Minutes extends React.Component {
    render() {
        const { match } = this.props;

        return (
            <Switch>
                <Route path={`${match.url}/index`} component={Index} />
                <Route path={`${match.url}/20191206`} component={I20191206} />
                <Route path={`${match.url}/20200213`} component={I20200213} />
                <Route path={`${match.url}/event-0001`} component={Event1} />
                <Route path={`${match.url}/event-0003`} component={Event3} />
                <Route path={`${match.url}/event-0004`} component={Event4} />
                <Route path={`${match.url}/event-0005`} component={Event5} />
                <Route path={`${match.url}/event-0007`} component={Event7} />
                <Route path={`${match.url}/event-0008`} component={Event8} />
                <Route path={`${match.url}/event-0009`} component={Event9} />
            </Switch>
        );
    }
}
